@import url(https://fonts.googleapis.com/css2?family=Margarine&display=swap);
.goods-list{list-style-type:none;margin-top:20px;width:50%;display:grid;grid-row-gap:5px;row-gap:5px}.goods-list__item{padding:10px 15px;width:100%;border-radius:20px;background-color:rgba(122,124,226,0.28);cursor:pointer}

*{box-sizing:border-box}body,h1,ul{margin:0}html{font-family:"Margarine", cursive;color:#7adaeb}.main{height:100vh;display:flex;flex-direction:column;align-items:center;padding-top:60px;background-image:linear-gradient(-225deg, #3d4e81 0%, #5753c9 48%, #6e7ff3 100%)}.main__title{text-transform:uppercase;font-size:52px}.main__buttons{margin-top:10px;display:flex;flex-wrap:wrap;grid-column-gap:10px;-webkit-column-gap:10px;column-gap:10px}.main__btn{border:none;border-radius:30%;width:80px;height:50px;font-size:18px;color:aliceblue;font-family:inherit;background-color:#9c92d6;cursor:pointer;transition:0.3s ease-in-out}.main__btn:focus,.main__btn:active{outline:none}.main__btn:hover{background-color:rgba(233,157,71,0.86)}

